.dialogs-header {
        display: grid;
        grid-template-columns: max-content min-content;
        height: 50px;
        align-items: center;
        padding: 0 20px 0 20px ;
        justify-content: space-between;
}
.dialogs-header-user {
        font-size: 18px;
        font-weight: 600;
        color: rgb(83, 83, 83);
        font-family: Arial, Helvetica, sans-serif;
}
.dialogs-header-online {
        color: rgb(6, 185, 36);
        font-weight: 600;
}